const initialData = {
  apiKey: 'AIzaSyAnK4eAqNgnw60JFm3rhQ2m8peKUJtlYAQ',
  snippetLength: 25,
  nextPageToken: '',
  inputValue: '',
};

const getVideos = () => {
  let data;
  initialData.inputValue = document.querySelector('#search').value;

  return fetch(
    `https://www.googleapis.com/youtube/v3/search?key=${
      initialData.apiKey
    }&type=video&part=snippet&maxResults=${initialData.snippetLength}&q=${
      initialData.inputValue
    }&pageToken=${initialData.nextPageToken}`,
  )
    .then(res => res.json())
    .then((jsonRes) => {
      data = jsonRes;
      initialData.nextPageToken = data.nextPageToken;
      const videosParams = jsonRes.items.map(e => e.id.videoId).join(',');

      return fetch(
        `https://www.googleapis.com/youtube/v3/videos?key=${
          initialData.apiKey
        }&id=${videosParams}&part=snippet,statistics`,
      )
        .then(res => res.json())
        .then((secondJsonRes) => {
          for (let i = 0, len = data.items.length; i < len; i += 1) {
            data.items[i].viewCount = secondJsonRes.items[i].statistics.viewCount;
          }
          return data;
        });
    });
};

export default getVideos;
