import getVideos from './request';
import { addNode, paginationDraw, goToPage } from './draw';

let startSwipePosition;
let endSwipePosition;
let swipeStart = false;

const searchHandler = () => {
  document.querySelector('#search').addEventListener('keyup', (e) => {
    if (e.keyCode === 13) {
      getVideos()
        .then((data) => {
          addNode(data, true);
          document.querySelector('.pagination-wrapper').classList.remove('hidden');
        })
        .then(() => paginationDraw(0))
        .then(() => goToPage(0));
    }
  });
};

const swipeBegin = (e) => {
  if (
    e.target.nodeName === 'INPUT'
    || document.querySelector('.video-list').children.length === 0
  ) {
    return;
  }
  swipeStart = true;

  startSwipePosition = e.clientX || e.targetTouches[0].clientX;
};

const swipeEnd = (e) => {
  if (!swipeStart) {
    return;
  }

  const pageNumber = Number(document.querySelector('.pagination-panel input:checked + label').innerHTML) - 1;

  endSwipePosition = e.clientX || e.changedTouches[0].clientX;

  const event = new Event('swipeEnd', {
    bubbles: true,
  });

  if (startSwipePosition - endSwipePosition > 10) {
    goToPage(pageNumber + 1);
    document.querySelector(`#page${pageNumber + 1}`).dispatchEvent(event);
  } else if (endSwipePosition - startSwipePosition > 10 && pageNumber > 0) {
    goToPage(pageNumber - 1);
    document.querySelector(`#page${pageNumber - 1}`).dispatchEvent(event);
  }

  swipeStart = false;
};

const swipeMouseHandler = () => {
  document.querySelector('body').addEventListener('mousedown', swipeBegin);
  document.querySelector('body').addEventListener('mouseup', swipeEnd);
};

const swipeTouchHandler = () => {
  document.querySelector('body').addEventListener('touchstart', swipeBegin);
  document.querySelector('body').addEventListener('touchend', swipeEnd);
};

const paginationHandler = () => {
  document.querySelector('.pagination-panel ul').addEventListener('change', (e) => {
    const pageNumber = Number(document.querySelector('.pagination-panel input:checked + label').innerHTML) - 1;

    goToPage(pageNumber);
    const event = new Event('swipeEnd', {
      bubbles: true,
    });
    e.target.dispatchEvent(event);
  });
};

const uploadHandler = () => {
  document.querySelector('.pagination-panel ul').addEventListener('swipeEnd', (e) => {
    const pageNumber = Number(document.querySelector('.pagination-panel input:checked + label').innerHTML) - 1;

    if (pageNumber >= Math.floor(e.currentTarget.children.length * 0.6)) {
      getVideos()
        .then(data => addNode(data, false))
        .then(() => paginationDraw(pageNumber));
    }
  });
};

const resizeHandler = () => {
  const widthChange = () => {
    if (document.querySelector('.pagination-panel ul').children.length > 0) {
      paginationDraw();
    }
  };

  if (matchMedia) {
    const mediaQuery1 = window.matchMedia('(max-width: 730px)');
    mediaQuery1.addListener(widthChange);

    const mediaQuery2 = window.matchMedia('(max-width: 1100px)');
    mediaQuery2.addListener(widthChange);

    const mediaQuery3 = window.matchMedia('(max-width: 1460px)');
    mediaQuery3.addListener(widthChange);
  }
};

const buttonsHandler = () => {
  document.querySelector('#left-button').addEventListener('click', () => {
    const pageNumber = Number(document.querySelector('.pagination-panel input:checked + label').innerHTML) - 11;

    goToPage(pageNumber >= 0 ? pageNumber : 0);
  });

  document.querySelector('#right-button').addEventListener('click', () => {
    const pageNumber = Number(document.querySelector('.pagination-panel input:checked + label').innerHTML) + 9;
    const maxNumber = document.querySelector('.pagination-panel ul').children.length - 1;

    goToPage(pageNumber < maxNumber ? pageNumber : maxNumber);

    const event = new Event('swipeEnd', {
      bubbles: true,
    });
    document.querySelector('.pagination-panel ul').dispatchEvent(event);
  });
};

const listen = () => {
  searchHandler();

  swipeMouseHandler();
  swipeTouchHandler();

  paginationHandler();
  uploadHandler();
  resizeHandler();

  buttonsHandler();
};

export default listen;
