let pageLength = 0;
let pagesCount = 0;

const drawMarkup = () => {
  const body = document.querySelector('body');

  body.innerHTML = `
    <form action="#">
      <div id="search-form">
        <i class="fa fa-search-plus" aria-hidden="true"></i>
        <input id="search" type="text" placeholder="Write a request and press inter">
      </div>
    </form>

    <div class="slider-wrapper video-panel noselect">
      <ul class="video-list slider"></ul>
    </div>

    <div class="pagination-wrapper slider-wrapper hidden">
      <button id="left-button" title="10 pages back">
        <i class="fa fa-fast-backward" aria-hidden="true"></i>
      </button>
      <div class="pagination-panel slider-wrapper">
        <ul class="slider"></ul>
      </div>
      <button id="right-button" title="10 pages forward">
        <i class="fa fa-fast-forward" aria-hidden="true"></i>
      </button>
    </div>
    `;
};

const addNode = (data, isRedraw) => {
  const videoList = document.querySelector('.video-list');

  if (isRedraw) {
    document.querySelector('.video-list').innerHTML = '';
  }

  let publishedDate;
  data.items.forEach(item => {
    publishedDate = new Date(item.snippet.publishedAt);

    videoList.innerHTML += `
    <li class="video">
        <a href="https://www.youtube.com/watch?v=${item.id.videoId}" target="_blank">
          <img src="${item.snippet.thumbnails.medium.url}">
        </a>
        <div class="video-description">
          <h3>
            <a class="video-name" href="https://www.youtube.com/watch?v=${
              item.id.videoId
            }" target="_blank">${item.snippet.title}</a>
          </h3>
          <p class="video-channel-cnt">
            <i class="fa fa-user-o" aria-hidden="true"></i>
            <a class="video-channel" href="https://www.youtube.com/channel/${
              item.snippet.channelId
            }" target="_blank">${item.snippet.channelTitle}</a>
          </p>
          <p class="video-channel-cnt"><i class="fa fa-calendar" aria-hidden="true"></i> &nbsp;&nbsp;&nbsp;${publishedDate.toDateString()}</p>
          <p class="video-channel-cnt"><i class="fa fa-eye-slash" aria-hidden="true"></i> &nbsp;&nbsp;&nbsp;${
            item.viewCount
          }</p>
          <p>${item.snippet.description}</p>
        </div>
      </li>
    `;
  });
};

const goToPage = pageNumber => {
  if (pageNumber >= pagesCount) {
    return;
  }

  const width = document.querySelector('.video-panel').offsetWidth;
  document.querySelector('.video-list').style.marginLeft = `${-width * pageNumber}px`;

  if (pageNumber === 0 || pageNumber === 1) {
    document.querySelector('.pagination-panel ul').style.marginLeft = '-80px';
  } else {
    document.querySelector('.pagination-panel ul').style.marginLeft = `${-40 * pageNumber}px`;
  }

  document.querySelector(`#page${pageNumber}`).checked = true;
};

const paginationDraw = currentPage => {
  pageLength = document.querySelector('.video-panel').offsetWidth / 350;
  pagesCount = Math.floor(document.querySelector('.video-list').children.length / pageLength);

  const paginationList = document.querySelector('.pagination-panel ul');
  paginationList.innerHTML = '';

  for (let i = 0; i < pagesCount; i += 1) {
    paginationList.innerHTML += `
      <li>
        <input id="page${i}" type="radio" name="page">
        <label for="page${i}"  title="${i + 1}">${i + 1}</label>
      </li>
    `;
  }

  if (currentPage === undefined) {
    const videoList = document.querySelector('.video-list');
    const videoPanel = document.querySelector('.video-panel');

    let page = currentPage;
    page = Math.floor(-parseInt(videoList.style.marginLeft || 0, 10) / videoPanel.offsetWidth);
    goToPage(page);
  }

  if (currentPage < pagesCount) {
    document.querySelector(`#page${currentPage}`).checked = true;
  }
};

export { drawMarkup, addNode, paginationDraw, goToPage };
